<template>
  <div class="main-page">
    <div v-if="dataList.Content">
      <div class="newTitle">
        <!-- 上边的大标题 -->
        <div class="title">
          <span>{{ dataList.Title }}</span>
        </div>
        <!-- 下边的一行 -->
        <div class="source-wrap">
          <div>
            <span>发布人:</span><span>{{ dataList.CreateName }}</span>
          </div>

          <div>
            <span>发布时间:</span
            ><span>{{ $time(dataList.CreateTime).format("YYYY-MM-DD") }}</span>
          </div>
        </div>
        <div>
          <img class="showImg psd" :src="src" alt="" style="width:100%;margin-top:10px; display:block">
        </div>
      </div>
      <div class="newContent">
        <span v-html="dataList.Content"></span>
      </div>
    </div>
    <div v-else class="load">
      <van-loading type="spinner" />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dataList: {},
      Id: this.$route.query.Id,
      src:this.$route.query.src,
      show: false,
    };
  },
  methods: {
    CMSContentDetails() {
      this._api
        .CMSContentDetails({
          Id: this.Id,
        })
        .then((res) => {
          this.dataList = res.data;
        });
    },
  },
  created() {
    this.CMSContentDetails();
    setTimeout((this.show = true), 5000);
  },
};
</script>

<style lang="scss" scoped>
.main-page {
  padding: 0 15px;
  .scroll-Y {
    height: 100%;
  }

  .content-wrap {
    padding: 30rpx;
  }

  .newTitle {
    .title {
      display: block;
      text-align: center;
      font-weight: 600;
      padding-top: 15px;
      font-size: 18px;
      color: #303133;
      line-height: 40px;
      border-bottom: 1px solid #f2f2f4;
      text-align: center;
    }
  }

  .source-wrap {
    display: flex;
    justify-content: space-between;
    line-height: 82rpx;
    font-size: 13px;
    padding-top: 15px;
    color: #666;
    text {
      font-size: 28rpx;
    }
  }

  .newContent {
    margin-top: 20px;
    margin-bottom: 0px;
    padding: 0px;
    line-height: 42px;
    font-size: 18px;
    color: rgb(51, 51, 51);
    text-align: justify;
    font-family: 微软雅黑;
    white-space: normal;
    background-color: rgb(255, 255, 255);
  }
}
</style>